
























































































































































































































































































































































































import Vue from 'vue';
import axios from 'axios';

export default Vue.extend({
	name: 'App',
	data: () => ({
		isInit: false,
		primaryColor: '#021254',
		drawer: false,
		group: null,
		snackbar: false,
		emailSent: false,
		isSending: false,
		features: [
			{
				icon: 'mdi-account-tie-voice',
				title: 'Confident in speaking English',
				text:
					'From my experience living in Aus for 2 years, I became confident in speaking English.',
			},
			{
				icon: 'mdi-presentation',
				title: 'Great Presentation Skill',
				text:
					'I have great presentation skill which is one of my strength at work.',
			},
			{
				icon: 'mdi-account-group',
				title: 'A very sociable',
				text:
					'I am very sociable and has great interpersonnal relationship with people. I am easy-going and mostly get along very well my co-workers.',
			},
		],
		item: 1,
		experiences: [
			{
				title:
					'Work in Quality Control Team of OB Beer Company(subsidiary of AB InBev.)',
				subtitle: '2019.06 ~ (Now)',
			},
			{
				title:
					"Graduate from Choongbuk National Unv. (a Bachelor's degree of Microbiology)",
				subtitle: '2019.02.',
			},
			{
				title: 'Working Holiday in Australia(Sydney, Melbourne, Perth)',
				subtitle: '2016.06 ~ 2018.06',
			},
		],
		email: {
			from: '',
			subject: '',
			text: '',
		},
		emailSender: '',
	}),

	mounted() {
		this.isInit = true;
	},
	watch: {
		group() {
			this.drawer = false;
		},
	},

	methods: {
		async sendMail() {
			try {
				this.email.subject = `[${this.emailSender}님이 포트폴리오에서 보내신 메일] ${this.email.subject}`;
				this.email.text = `from: ${this.email.from} \n${this.email.text}`;
				this.isSending = true;
				const result = await axios({
					method: 'get',
					url: '/mail',
					data: {
						...this.email,
					},
				});
				console.log(result);
				this.emailSent = true;
				this.snackbar = true;
			} catch (err) {
				console.log(err);
				this.emailSent = false;
				this.snackbar = true;
			}
			this.isSending = false;
			this.resetEmail();
		},
		resetEmail() {
			this.email = {
				from: '',
				subject: '',
				text: '',
			};
			this.emailSender = '';
		},
	},
});
